import React from "react";
import { MeshDistortMaterial, Sphere } from "@react-three/drei";

export default function AnimatedSphere() {
  return (
    <Sphere visible args={[1.5, 100, 200]} scale={1.5}>
      <MeshDistortMaterial
        color="#565555"
        attach="material"
        distort={0.5}
        speed={1.75}
        roughness={0}
      />
    </Sphere>
  );
}
